<template>
    <div class="choice">
        <div class="choice-banner">
          
            <div @touchstart="abc" ref="bigBox" class="big-box">
                <div ref="imgBox" v-for="(img,index) in imgList" :key="index" class="img-box">
                    <img :src="img.url" class="auto-img">
                </div>
            </div>

            <ul class="list">
                <li></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
          
        </div>

        <div class="my-books">
            
            <div class="books-item">
                <router-link tag="div" to="/bookcase">
                    <div class="book-img">
                        <img class="auto-img" src="../../public/images/书架.png">
                    </div>
                     <span>书架</span>
                </router-link>
               
            </div>
            <div class="books-item">
                <router-link tag="div" to="./login">
                    <div class="book-img">
                        <img class="auto-img" src="../../public/images/书单.png">
                     </div>
                    <span>书单</span>
                </router-link>
                
            </div>
            <div class="books-item">
                <router-link tag="div" to="./login">
                    <div class="book-img">
                         <img class="auto-img" src="../../public/images/漫画.png">
                  </div>
                  <span>漫画</span>
                </router-link>
                
            </div>
            <div class="books-item">
                <router-link tag="div" to="./login">
                    <div class="book-img">
                    <img class="auto-img" src="../../public/images/我的.png">
                </div>
                <span>我的</span>
                </router-link>
                
            </div>
        </div>

        <BookItem></BookItem>

   </div>
</template>

<script>

import BookItem from '../components/BookItem.vue'
import { setTimeout } from 'timers';

export default {
    name : "Choice",
    data(){
        return{
            imgList:[ 
            {url:require('../../public/images/a1.jpg'),fade:false},
            {url:require('../../public/images/a2.jpg'),fade:false},
            {url:require('../../public/images/a3.jpg'),fade:false},
            {url:require('../../public/images/a4.jpg'),fade:false},
            {url:require('../../public/images/a1.jpg'),fade:false},
           ],
           show:true,
           imgIndex:1,
           imgLeft:0,
           lr:true
        } 
    },
   components:{
       BookItem
   },
    methods:{

        abc(el){

            this.imgIndex++
            //获取照片宽度
            let imgBox  = this.$refs.imgBox[0]
            let imgBoxWidth = parseInt(getComputedStyle(imgBox).width)

            let bigBox  = this.$refs.bigBox
            
            let startX = el.touches[0].pageX
            
            let self =  this
            el.target.ontouchmove = function(e){
                      
                let moveX = parseInt(e.touches[0].pageX)
                
                //手指移动像素
                let index = self.imgIndex - 2
                if(self.lr){
                     bigBox.style.left = index * -imgBoxWidth + moveX - startX - 375 + 'px'
                }else{
                    bigBox.style.left = index * -imgBoxWidth + moveX - startX  + 'px'
                }
            
                //离开屏幕事件
                this.ontouchend = function(e){
                  
                    //右滑
                    if(startX > moveX){
                       self.lr = true
                        if(self.imgIndex == 4){
                           
                            bigBox.style.left = 0
                            self.imgIndex = 0
                           
                        }else{
                            bigBox.style.left = self.imgIndex * -imgBoxWidth + 'px'
                            bigBox.style.transition = 'all .4s linear'
                        }
                      
                    //左滑
                    }else{
                        self.lr = false
                        
                        if(self.imgIndex == 2){
                            
                            bigBox.style.left = '-1500px'
                            self.imgIndex = 5
                        }else{
                            self.imgIndex = self.imgIndex - 2
                            bigBox.style.left = (self.imgIndex) * -imgBoxWidth + 'px'
                            console.log(self.imgIndex * -imgBoxWidth )
                            bigBox.style.transition = 'all .4s linear'
                        }         
                    }
                }
            }          
        },

    
    },


    props:['sexShow']

}
</script>

<style scoped>
    .choice-banner{
        width: 100%;
        height: 120px;
        overflow: hidden;
    }
    .big-box{
        width: 1875px;
        height: 100%;
        position: relative;
        left: -375px;
    }
    .img-box{
        width: 375px;
        float: left;
       
    }
   .my-books{
       display: flex;
       text-align: center;
       font-size: 13px;
       margin-bottom: -10px;
       padding-bottom: 8px;
       border-bottom: 10px solid rgb(243, 243, 243);
   }
   .my-books>span{
        color: #ccc;
   }
   
   .my-books>div{
       flex:1
   }
   .book-img{
       width: 45px;
       padding: 10px 0;
       margin: 0 auto;
   }
   .boo{
       margin-top: 20px;
       height: 300px;
   }
</style>